@mixin hvr-underline-from-left($color: #f00) {
  display: flex;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 100%;
    bottom: 0;
    height: 4px;
    -webkit-transition-property: right;
    transition-property: right;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
    background: $color;
  }
  &:hover::before, &:focus::before, &:active::before {
    right: 0;
  }
}
.hvr-underline-from-left-red {
  @include hvr-underline-from-left($color: #f00);
}
.hvr-underline-from-left-orange {
  @include hvr-underline-from-left($color: #ff7100);
}
.hvr-underline-from-left-yellow {
  @include hvr-underline-from-left($color: #ffe000);
}
.hvr-underline-from-left-green {
  @include hvr-underline-from-left($color: #07ff00);
}
.hvr-underline-from-left-blue {
  @include hvr-underline-from-left($color: #0041ff);
}
.hvr-underline-from-left-purple {
  @include hvr-underline-from-left($color: #b100ff);
}