@use 'Hovers';

body {
  font-family: 'Raleway';
  line-height: 24px;
  overflow-x: hidden;
}
a {
  text-decoration: none;
  color: #000;
}
hr {
  width: 100%;
  border: .5px solid #777;
  margin: 0;
}
li {
  list-style-position: inside;
  list-style-type: disclosure-closed;
}
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
  border-radius: 2px;
}
::-webkit-scrollbar-thumb {
  background: #ccc; 
}
::-webkit-scrollbar-thumb:hover {
  background: #c9c9c9; 
}

.align-center {
  align-self: center;
}
.no-margin {
  margin-block-start: 0;
  margin-block-end: 0;
}
.no-top-margin {
  margin-block-start: 0;
}
.no-bottom-margin {
  margin-block-end: 0;
}


%content-section {
  background-color: #ccc;
  border: .5px solid #777;
  border-radius: 6px;
}
%headerBackground {
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: repeat-y;
}


.content-large-section {
  @extend %content-section;
  width: 80%;
  margin: 20px auto;
}
.content-small-section {
  @extend %content-section;
  width: 12.5%;
  min-width: 12.5%;
  height: auto;
  padding: 10px;
  margin: 0 40px;
}
.flex-col {
  display: flex;
  flex-direction: column;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
.info-title {
  display: flex;
  position: sticky;
  top: 0;
  flex-direction: column;
  height: 70px;
  background-size: cover;
  z-index: 100;
}
.info-title p {
  font-size: 40px;
  text-align: right;
  margin: auto 10px;
}
.nav-tab {
  flex: 1;
  font-size: 2vw;
  cursor: pointer;
  margin: 0;
  align-items: center;
}
.nav-tab p {
  margin: 0 10px 0 auto;
}
.small-img {
  height: 30px;
  width: auto;
  margin: auto 10px auto 0px;
}
.small-vertical-pad {
  padding: 0px 10px;
}
.standard-margin {
  width: 95%;
  margin: 10px auto;
}
.text-center {
  text-align: center;
}

#info {
  width: 86%;
  margin-left: auto;
}
#nav-bar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 14%;
  height: 100%;
  background-color: #ccc;
  border-right: 1px solid #777;
  box-shadow: 0 0 5px 0 #111;
  align-content: space-around;
}
#nav-desc {
  flex: 1;
  font-size: 1vw;
  text-align: right;
  margin: 0;
}
#nav-name {
  flex: 0 0 auto;
  height: 15%;
  margin: 15px;
  user-select: none;
}
#nav-title {
  flex: 2;
  font-size: 3vw;
  line-height: 3vw;
  text-align: right;
  margin: 0;
}
#about {
  @extend %headerBackground;
  background-image: url('backgrounds/graph-1.png');
  #text {
    width: 80%;
    margin: 16px auto;
  }
}
#contact {
  @extend %headerBackground;
  background-image: url('backgrounds/graph-5.png');
  .contact-row {
    width: auto;
    margin: 10px auto;
    i {
      font-size: 100px;
      margin-right: 30px;
    }
    a, h2 {
      margin: auto 0;
    }
  }
}
#experience {
  @extend %headerBackground;
  background-image: url('backgrounds/graph-2.png');
  .experience-header { 
    width: auto;
    margin: 10px auto 0 auto;
    justify-content: center;
  }
}
#projects {
  @extend %headerBackground;
  background-image: url('backgrounds/graph-3.png');
  .project-entry {
    flex: 1;
  }
  .project-img, .project-video {
    width: 80%;
    border: 1px solid #777;
    box-shadow: 0 0 3px 0 #444;
    margin: 0 auto;
  }
  .project-row {
    display: flex;
    width: 90%;
    margin: 10px auto;
    justify-content: space-around;
  }
}
#resume {
  @extend %headerBackground;
  background-image: url('backgrounds/graph-6.png');
  height: 1000px;
  embed {
    padding: 16px;
  }
  a {
    display: none;
  }
}
#skills {
  @extend %headerBackground;
  background-image: url('backgrounds/graph-4.png');
  .skill-img {
    flex-grow: 1;
    width: 80%;
    height: auto;
    object-fit: contain;
    margin: 0 auto;
  }
  $skill-row-height: -236.5px;
  .skill-row {
    width: 90%;
    padding-bottom: 6px;
    margin: 20px auto;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    scroll-behavior: smooth;
    &::-webkit-scrollbar {
      display: none;
    }
    &-holder {
      position: relative;
    }
    &-scroll {
      position: absolute;
      font-size: 40px;
      height: 40px;
      top: calc(50% + 24px);
      transform: translateY(-50%);
      cursor: pointer;
      z-index: 2;
      &-left {
        @extend .skill-row-scroll;
        left: 5%;
      }
      &-right {
        @extend .skill-row-scroll;
        right: 5%;
      }
    }
    &-fade {
      --right-mask: linear-gradient(to right, 
        rgba(0,0,0, 1) 0,   rgba(0,0,0, 1) 90%, 
        rgba(0,0,0, 0) 95%, rgba(0,0,0, 0) 0
      ) 100% 50% / 100% 100% repeat-x;
      --left-mask: linear-gradient(to left, 
        rgba(0,0,0, 1) 0,   rgba(0,0,0, 1) 90%, 
        rgba(0,0,0, 0) 95%, rgba(0,0,0, 0) 0
      ) 100% 50% / 100% 100% repeat-x;
      --all-mask: linear-gradient(to left, 
        rgba(0,0,0, 0) 0,   rgba(0,0,0, 0) 5%, 
        rgba(0,0,0, 1) 10%,   rgba(0,0,0, 1) 90%, 
        rgba(0,0,0, 0) 95%, rgba(0,0,0, 0) 0
      ) 100% 50% / 100% 100% repeat-x;
      &-left {
        @extend .skill-row-fade;
        position: sticky;
        z-index: 1;
        -webkit-mask: var(--left-mask); 
        mask: var(--left-mask);
      }
      &-right {
        @extend .skill-row-fade;
        position: sticky;
        z-index: 1;
        -webkit-mask: var(--right-mask); 
        mask: var(--right-mask);
      }
      &-all {
        @extend .skill-row-fade;
        position: sticky;
        z-index: 1;
        -webkit-mask: var(--all-mask); 
        mask: var(--all-mask);
      }
    }
  }
  .skill-text {
    height: 24px;
    margin: 10px 0;
  }
}

@media all and (max-width:850px) {
  #nav-bar {
    display: none;
  }
  #info {
    width: 100%;
    margin-left: 0px;
  }
  #experience {
    h2 {
      width: 70%;
    }
    ul {
      width: 80%;
      padding: 0;
      li {
        margin: 10px 0;
      }
    }
    .job-info {
      width: 90%;
      margin: 0 auto;
    }
  }
  #skills {
    .content-small-section {
      width: 100px;
      min-width: 100px;
    }
  }
  #contact {
    i {
      width: fit-content;
      margin: 0 auto;
    }
    h2 {
      display: none;
    }

    #content-holder {
      flex-direction: row;
    }
  }

  .project-row, .contact-row {
    flex-direction: column;
  }
  #resume {
    @extend %headerBackground;
    background-image: url('backgrounds/graph-6.png');
    height: fit-content;
    embed {
      display: none;
    }
    a {
      display: block;
      font-size: 40px;
      margin: 24px auto;
    }
  }
}